body {
  margin: 0; // removes margin around whole site body
  background-color: rgb(231, 235, 255);
  scroll-behavior: smooth;
  // background-color: rgb(68, 68, 68);
}

.welcome {
  // border: 2px solid red;
  margin-left: 5%;
  width: 90%;
  height: 10vh;
  background-color: rgb(255, 255, 255);
  // border-bottom: 3px solid orangered;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: sticky; // sticky header
  top: 0;

  .logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(62, 113, 129);
    font-family: "Merriweather", "Gill Sans", "Gill Sans MT", Calibri,
      "Trebuchet MS", sans-serif;
    border-right: 2px solid white;
    width: 40%;
    height: 100%;

    img {
      height: 65px;
      width: 65px;
      margin-left: 35px;
      margin-top: 5px;
    }

    h1 {
      margin-left: 20px;
      font-size: 2.5em;
    }
  }

  .profile {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    // border-left: 4px solid white;
    width: 60%;
    height: 100%;

    
    #profile-button {
      margin-right: 5%;
      margin-top: 1px;
      border-radius: 50%;
      border: 1px solid black;
    }

    #settings {
      display: none;
    }
    
    .logout_button {
      border: none;
      color: black;
      border-radius: 100%;
      width: 48px;
      height: 48px;
      padding: 5px;
      margin-right: 20px;
      font-size: 12px;
      font-family: "Merriweather";
      box-shadow: -2px 2px 5px rgba(0, 0, 0, 0.514);
      background-color: gold;
    }
    button:hover {
      box-shadow: none;
    }

    h5 {
      font-family: "Merriweather";
      margin-right: 10px;
    }
  }
}

.dropshut {
  height: 0vh;
  width: 30%;
  margin-left: 75%;
  position: absolute;
  overflow: hidden;
  li {
    color: white;
    padding-bottom: 10px;
  }
  li:hover {
    color: gold;
  }
}

.dropopen {
  z-index: 3;
  height: 20vh;
  width: 20%;
  margin-left: 75%;
  position: absolute;
  background-color: orangered;
  animation-name: slide-down;
  animation-duration: 0.3s;
  li {
    color: white;
    padding-bottom: 10px;
    list-style-type: none;
  }
  li:hover {
    color: gold;
  }
}

@keyframes slide-down {
  0% {
    height: 0vh;
    width: 20%;
    overflow: hidden;
  }
  // 99% {
  //     height: 20vh;
  //     width: 20vw;
  //     overflow: hidden;
  // }
  100% {
    height: 20vh;
    width: 20%;
    overflow: visible;
  }
}

a {
  text-decoration: none;
}

@import "./NavbarMobile.scss";
