body {
  margin: 0;
  background-color: #e7ebff;
  scroll-behavior: smooth; }

.welcome {
  margin-left: 5%;
  width: 90%;
  height: 10vh;
  background-color: white;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: sticky;
  top: 0; }
  .welcome .logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #3e7181;
    font-family: "Merriweather", "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    border-right: 2px solid white;
    width: 40%;
    height: 100%; }
    .welcome .logo img {
      height: 65px;
      width: 65px;
      margin-left: 35px;
      margin-top: 5px; }
    .welcome .logo h1 {
      margin-left: 20px;
      font-size: 2.5em; }
  .welcome .profile {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: 60%;
    height: 100%; }
    .welcome .profile #profile-button {
      margin-right: 5%;
      margin-top: 1px;
      border-radius: 50%;
      border: 1px solid black; }
    .welcome .profile #settings {
      display: none; }
    .welcome .profile .logout_button {
      border: none;
      color: black;
      border-radius: 100%;
      width: 48px;
      height: 48px;
      padding: 5px;
      margin-right: 20px;
      font-size: 12px;
      font-family: "Merriweather";
      box-shadow: -2px 2px 5px rgba(0, 0, 0, 0.514);
      background-color: gold; }
    .welcome .profile button:hover {
      box-shadow: none; }
    .welcome .profile h5 {
      font-family: "Merriweather";
      margin-right: 10px; }

.dropshut {
  height: 0vh;
  width: 30%;
  margin-left: 75%;
  position: absolute;
  overflow: hidden; }
  .dropshut li {
    color: white;
    padding-bottom: 10px; }
  .dropshut li:hover {
    color: gold; }

.dropopen {
  z-index: 3;
  height: 20vh;
  width: 20%;
  margin-left: 75%;
  position: absolute;
  background-color: orangered;
  animation-name: slide-down;
  animation-duration: 0.3s; }
  .dropopen li {
    color: white;
    padding-bottom: 10px;
    list-style-type: none; }
  .dropopen li:hover {
    color: gold; }

@keyframes slide-down {
  0% {
    height: 0vh;
    width: 20%;
    overflow: hidden; }
  100% {
    height: 20vh;
    width: 20%;
    overflow: visible; } }

a {
  text-decoration: none; }

@media (max-width: 720px) {
  .welcome .logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #3e7181;
    width: 50%;
    height: 100%; }
    .welcome .logo img {
      height: 2em;
      width: 2em;
      margin-left: 10px; }
    .welcome .logo h1 {
      margin-left: 5px;
      font-size: 1em; }
  .welcome .profile {
    width: 70%; }
    .welcome .profile .logout_button {
      width: 4em;
      height: 4em;
      margin-right: auto;
      font-size: 12px; }
    .welcome .profile button:hover {
      box-shadow: none; }
    .welcome .profile #profile-button {
      display: none; }
    .welcome .profile #settings {
      display: block;
      height: 1.5em;
      width: 1.5em;
      margin: auto; }
    .welcome .profile h5 {
      font-size: 1.2em;
      display: none; } }
